/* Estilos letras */
@import url('https://fonts.googleapis.com/css2?family=Comfortaa&family=Jost:ital,wght@0,100;1,200;1,300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Comfortaa&family=Jost:ital,wght@0,100;1,200;1,300&family=News+Cycle&display=swap');


body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
